#app2 .tab-bar{
    background: antiquewhite;
    display:flex;
}
#app2 .tab-bar > li{
    border: 1px solid grey;
    width:50%;
}
#app2 .tab-bar > li.selected{
    background: darkorange;
}
#app2 .tab-content > li{
    display: none;
}

#app2 .tab-content > li.active{
    display:block
}
